.dialog {
    width: 75%;
    border-radius: 0px 8px 8px 0px;
    display: flex;
    flex-direction: column;
    background-color: #ffff;
    box-shadow: 0px 0px 3px 0 rgba(0, 0, 0, 0.5);
}

.header {
    flex: 0 0 120px; 
    max-width: 100%;
    max-height: 170px;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.25);
    z-index: 2;
}

.fon {
    min-height: 32px;
    background-color: #005475;
    border-radius: 0px 8px 0px 0px;
}

.pomoshnikSearch {
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 2%;
    padding-right: 2%;
}

.pomoshnik {
    display: flex;
    flex-direction: row;
    column-gap: 30px;
    align-items: center;
}

.iconBack:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 50%;
    cursor: pointer;
}

.search {
    -webkit-appearance: none;
    /* Удаляет стандартные стили WebKit */
    width: 10vw;
    /* Регулирует ширину */
    height: 50%;
    /* Регулирует высоту */
    border-bottom: 1px solid #ccc;
    /* Добавляет границу */
}

.spanPomoshnik {
    align-self: flex-start;
    font-family: "Montserrat";
    font-size: 12px;
    position: relative;
    font-weight: 600;
}

.spanPomoshnik span {
    color: rgb(58, 58, 58);
}

.spanPomoshnik::after {
    content: 'ПРОГРАММЫ И ПРОЕКТЫ';
    white-space: nowrap;
    position: absolute;
    left: 0;
    bottom: -15px;
    font-weight: 600;
    font-size: 12px;
}

.editText {
    padding: 0.1% 2%;
    max-width: 100%;
    background-color: #ffff;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 10px;
    row-gap: 10px;
}

.item {
    margin-right: 20px;
    width: 170px;
    max-width: 170px;
    overflow: hidden;
}

.itemLast {
    margin-right: 50px;
}

.nameList {
    color: #3A3A3A;
    font-family: "Montserrat";
    text-align: center;
    font-weight: 600;
}

.blockSelect {
    position: relative;
    margin-right: 30px;

    &>img:hover {
        background-color: rgb(240, 240, 240);
        border-radius: 20%;
    }
}

.blockSelect:hover .option {
    display: block;
}

.optionNumber li:nth-child(n+2):after {
    content: counter(item);
    position: absolute;
    bottom: 0;
    left: -15px;
    color: #B4B4B4;
    font-weight: 600;
    font-size: 14px;
    font-family: 'Montserrat';
}

.optionNumber {
    position: absolute;
    display: none;
    white-space: nowrap;
    right: 1%;
    background-color: #ffff;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    padding: 5px;
    padding-bottom: 8px;

    & .blackStrategy {
        margin-top: 10px;
        margin-bottom: 15px;
        font-family: 'Montserrat';
        font-weight: 400;
        font-size: 14px;
    }

    & .blackStrategy:before {
        content: '';
        position: absolute;
        height: 1px;
        width: 98%;
        bottom: 0;
        top: 45px;
        background-color: rgb(240, 240, 240);
    }

    & li {
        margin-left: 15px;
        counter-increment: item;
        margin-bottom: 15px;
    }

    & li:last-child {
        margin-bottom: 0;
    }

}

.optionNumber img {
    margin-right: 10px;
}

.optionNumber li:first-child {
    margin-top: 0px;
}

.optionNumber li {
    margin-top: 7px;
    position: relative;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 12px;
}

.optionNumber li:hover {
    height: 20px;
    background-color: rgb(240, 240, 240)
}

.optionNumber li:before {
    content: '';
    position: absolute;
    height: 1px;
    width: 100%;
    bottom: -4px;
    right: 0;
    background-color: rgb(240, 240, 240);
}

.blockSelect:hover .optionNumber {
    display: block;
}

/* <--! ---> */
.option {
    position: absolute;
    display: none;
    white-space: nowrap;
    right: 1%;
    background-color: #ffff;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    padding: 5px;
    padding-bottom: 8px;

    & li {
        counter-increment: item;
        margin-bottom: 15px;
    }

    & li:last-child {
        margin-bottom: 0;
    }

}

.option img {
    margin-right: 10px;
}

.option li:first-child {
    margin-top: 0px;
}

.option li {
    margin-top: 7px;
    position: relative;
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 12px;
}

.option li:hover {
    height: 20px;
    background-color: rgb(240, 240, 240)
}



.option li:before {
    content: '';
    position: absolute;
    height: 1px;
    width: 90%;
    bottom: -4px;
    right: 0;
    background-color: rgb(240, 240, 240);
}

.blockSelect:hover .option {
    display: block;
}

.iconSave {
    position: relative;
    margin-left: auto;
}

.iconSave:hover::after {
    content: "Сохранить";
    white-space: nowrap;
    position: absolute;
    width: 80px;
    height: 20px;
    bottom: -120%;
    left: -120%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3A3A3A;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
    z-index: 1000;
}

.image:hover {
    background-color: rgb(240, 240, 240);
    border-radius: 20%;
    cursor: pointer;
}

.itemName span {
    font-style: italic;
    font-size: 12px !important;
    font-family: 'Montserrat';
    font-weight: 400;
}

.select {
    width: 100%;
    max-width: 100%;
    position: relative;
    font-family: 'Montserrat';
    overflow: hidden;
    text-overflow: ellipsis;
}

.div {
    max-width: 100%;
    position: relative;
}

.div:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: rgb(240, 240, 240);
    z-index: 100;
}

.main {
    position: relative;
    flex: 1; 
    height: auto;
    width: 100%;
    overflow-y: auto;
    background-color: #B4B4B4;
    padding-top: 1%;
    padding-bottom: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 3%;
}


